    const w = 1000
    const h = 500
    const padding = 50

    const xScale = d3.scaleLinear()
      .domain([1947, d3.max(dataset, (d) => d[0].split('-')[0])])
      .range([padding, w - padding])

    const yScale = d3.scaleLinear()
      .domain([0, d3.max(dataset, d => d[1])])
      .range([padding, h - padding])

    const yScaleAxis = d3.scaleLinear()
      .domain([0, d3.max(dataset, d => d[1])])
      .range([h - padding, padding])

    const svg = d3.select('body')
      .append('svg')
      .attr('width', w)
      .attr('height', h)

    svg.append("text")
        .attr("x", 500)
        .attr("y", 100)
        .attr("text-anchor", "middle")
        .attr('class', 'title')
        .text("Gross Domestic Product");

    const tooltip = d3.select('body')
      .append('div')
      .attr('class', 'tooltip')

    svg.selectAll('rect')
      .data(dataset)
      .enter()
      .append('rect')
      .attr('x', d => {
        let year = d[0].split('-')[0]
        year = parseInt(year)
        switch (d[0].split('-')[1]) {
          case '01':
            break;
          case '04':
            year += .25
            break;
          case '07':
            year += .50
            break;
          case '10':
            year += .75
            break;
          default:
        }
        return xScale(year)
      })
      .attr('width', 4)
      .attr('y', d => h - yScale(d[1]))
      .attr('height', d => yScale(d[1]) - padding)
      .attr('class', 'bar')
      .on('mouseover', (d) => {
        tooltip.style('visibility', 'visible')
        tooltip.html('<b>$'+d[1] + ' Billion</b><br /> ' + d[0])
      })
      .on('mousemove', d => {
        tooltip.style('top', (d3.event.pageY - 50) + 'px').style('left', (d3.event.pageX + 5) + 'px')
      })
      .on('mouseout', () => tooltip.style('visibility', 'hidden'))

    // Axes
    const xAxis = d3.axisBottom(xScale)

    svg.append('g')
      .attr('transform', 'translate(0,' + (h - padding) + ')')
      .call(xAxis)

    const yAxis = d3.axisLeft(yScaleAxis)

    svg.append('g')
      .attr('transform', 'translate(' + padding + ', 0)')
      .call(yAxis)
  }
}
</script>

