  margin-top: 8%;
}

svg {
	background: lightGray;
}
.bar {
	fill: red;
}
.bar:hover {
	fill: Tomato;
}

.title {
  font-size: 24px;
}

.tooltip {
  position: absolute;
  z-index: 10;
  visibility: hidden;
  background: gray;
  border-radius: 5px;
  padding: 5px;
  width: 120px;
  height: 40px;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



